@import "./Variables.scss";

.about {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: column;
  height: 85vh;
  text-align: center;
  h1,
  h3,
  h4 {
    color: $primaryFontColor;
  }
  h1 {
    margin-top: 2vh;
    font-size: calc(1em + 3.5vw);
  }
  h3 {
    font-size: calc(0.5em + 1.5vw);
  }
  h4 {
    font-size: calc(0.4em + 1vw);
    margin-top: 5vh;
  }
}
