@import "./Variables.scss";

.nav-bar {
  position: fixed;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 10vh;
  width: 100%;
  background-color: $primaryBgColor;
  border-bottom: solid 2px $primaryFontColor;
  .branding {
    font-family: "Anton", sans-serif;
    cursor: pointer;
    animation: shrink 0.3s ease forwards;
    h1 {
      font-size: calc(0.4em + 2vw);
      color: $primaryFontColor;
    }
    &:hover {
      animation: expand 0.3s ease forwards;
    }
  }

  @keyframes expand {
    from {
      font-size: 2em;
    }
    to {
      font-size: 2.5em;
    }
  }

  @keyframes shrink {
    from {
      font-size: 2.5em;
    }
    to {
      font-size: 2em;
    }
  }

  .search-field {
    .input {
      border: 2px solid $primaryFontColor;
      height: 5vh;
      width: 30vw;
    }

    .button {
      height: 5vh;
      border: 2px solid $primaryFontColor;
      color: $primaryFontColor;
      background-color: $primaryBgColor;
      font-size: calc(0.5em + 0.7vw);
      &:hover {
        color: $primaryBgColor;
        background-color: $primaryFontColor;
      }
    }
  }

  .menu {
    font-family: "Hind", sans-serif;
    letter-spacing: 0.1em;
    text-align: center;
    ul {
      display: flex;
      align-items: center;
      justify-content: space-around;
    }
    li {
      margin-left: 2vw;
      padding: 0.5vh 2vw 0.5vh 2vw;
      display: inline;
      font-weight: bold;
      outline: solid 1px $primaryFontColor;
      &:hover {
        outline: solid 3px $primaryFontColor;
        border: 0;
      }
    }
  }

  .burger {
    display: none;
    cursor: pointer;
    img {
      height: 5vh;
    }
  }

  .active-page {
    color: $primaryBgColor;
    background-color: $primaryFontColor;
  }

  @media (max-width: 630px) {
    .menu ul {
      z-index: -33;
      position: absolute;
      top: 10vh;
      right: 0;
      width: 100%;
      height: 5vh;
      display: flex;
      align-items: center;
      justify-content: space-around;
      transform: translateY(-50%);
      opacity: 0;
      transition: all 0.2s ease-out;
      background-color: $primaryBgColor;
      border-bottom: $primaryFontColor 1px solid;
    }
    .show-nav {
      transform: translateY(0%);
      opacity: 1;
    }
    .burger {
      display: block;
    }
  }
}
