@import "./Variables.scss";
.list {
  margin-top: 9vh;
  padding-top: 2vh;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.container {
  margin: 1vh;
  flex: 45%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  border: solid 2px $primaryFontColor;
  border-radius: 10px;
  box-shadow: 0px 5px 10px $primaryFontColor;
  background-color: $primaryBgColor;
  text-align: start;

  h1,
  h5,
  p {
    color: $primaryFontColor;
  }

  h1 {
    text-align: center;
    font-size: 2em;
    padding: 0.5em;
    font-size: calc(1em + 2.5vw);
  }
  h5 {
    font-size: calc(0.4em + 1.8vw);
  }
  ol {
    padding: 1em;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    li {
      font-size: 13px;
      list-style-type: none;
      p {
        font-size: calc(0.4em + 1.5vw);
      }
    }
  }
  img {
    border: 1px solid $primaryFontColor;
    animation: shrink 0.5s ease forwards;
    &:hover {
      animation: expand 0.5s ease forwards;
    }
  }
}

@keyframes expand {
  from {
    border-radius: 10em;
  }
  to {
    border-radius: 1em;
  }
}

@keyframes shrink {
  from {
    border-radius: 1em;
  }
  to {
    border-radius: 10em;
  }
}
